@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap");
* {
  margin: 0;
  padding: 0;
  border: 0;
  line-height: 1.4em;
  vertical-align: baseline;
  text-decoration: none;
  outline: 0;
  box-sizing: border-box;
  background-color: transparent;
  font-family: "Montserrat", sans-serif;
}

header {
  width: 100%;
  height: 195px;
  background-color: black;
  font-weight: 400;
  display: flex;
}
header .head-content {
  width: 2560px;
  height: 100%;
  margin: 0 auto;
  padding: 0 200px;
  display: flex;
  justify-content: space-between;
}
header .head-content .logo {
  height: 100%;
  display: flex;
  align-items: center;
}
header .head-content .logo img {
  height: 70px;
}
header .head-content .menu {
  height: 100%;
  font-size: 30px;
  display: flex;
  flex-grow: 1;
  justify-content: right;
}
header .head-content .menu .menu-items {
  max-width: 950px;
  list-style: none;
  display: flex;
  flex-grow: 1;
  justify-content: space-around;
  align-items: center;
}
header .head-content .menu .menu-items li {
  border-bottom: 4px transparent solid;
  padding: 7px 10px;
}
header .head-content .menu .menu-items li.active {
  border-bottom: 4px white solid;
}
header .head-content .menu .menu-items li a {
  color: white;
}
header .head-content .menu .languages {
  width: 240px;
  margin-left: 60px;
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
header .head-content .menu .languages li {
  color: white;
  margin: 2px;
  padding: 5px 7px;
}
header .head-content .menu .languages li:hover {
  cursor: pointer;
  color: #ED2D32;
}
header .head-content .menu .languages li.active {
  background-color: white;
  color: black;
  font-weight: 700;
}
header .head-content .hamburger {
  display: none;
  width: 20px;
  height: 20px;
  margin-right: 20px;
}

.container {
  width: 2560px;
  min-height: 100vh;
  margin: 0 auto;
}

.background {
  position: absolute;
  width: 2560px;
  margin: 0 auto;
  z-index: -2;
}

.polygons {
  position: absolute;
  left: 0;
  width: 100%;
}
.polygons .shadow {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}
.polygons .shadow .polygon {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}
.polygons .shadow .polygon.white {
  background-color: white;
}
.polygons .shadow .polygon.black {
  background-color: black;
}

.polywrapper {
  position: relative;
  width: 2560px;
  height: 830px;
  margin: 0 auto;
}

.content {
  position: absolute;
  width: 2560px;
  margin: 0 auto;
}
.content.black {
  color: black;
}
.content.black span {
  border-bottom: black 5px solid;
}
.content.white {
  color: white;
}
.content.white .subtitle {
  font-weight: 400;
}
.content.white span {
  border-bottom: white 4px solid;
}
.content h1 {
  font-size: 85px;
  font-weight: 300;
  line-height: 80px;
  text-transform: uppercase;
}
.content h1 span {
  display: inline-block;
  min-width: 650px;
  padding-bottom: 40px;
  font-weight: 600;
  line-height: 80px;
}
.content h2 {
  font-size: 54px;
  font-weight: 700;
  line-height: 40px;
  text-transform: uppercase;
  margin-bottom: 50px;
}
.content .text {
  font-size: 24px;
  font-weight: 500;
  line-height: 36px;
  margin-bottom: 50px;
}
.content .subtitle {
  margin: 60px 0;
  font-size: 24px;
  font-weight: 800;
}
.content .button {
  min-width: 300px;
  width: max-content;
  padding: 16px 30px;
  margin: 20px 0;
  font-size: 25px;
  font-weight: 600;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.content .button.white {
  background-color: black;
  color: white;
}
.content .button.black {
  background-color: white;
  color: black;
  font-weight: 800;
}
.content .button img {
  height: 36px;
  margin-left: 40px;
}
.content .button.icon {
  justify-content: space-between;
}

.add-picture {
  position: absolute;
}

.quick-contact {
  height: 270px;
}
.quick-contact .polygons {
  height: 270px;
}
.quick-contact .polygons .polygon.plg-1 {
  clip-path: polygon(0% 0%, calc((100vw - 2560px) / 2 + 1555px) 0%, calc((100vw - 2560px) / 2 + 1555px + 270px) 100%, 0% 100%);
}
.quick-contact .qc-content {
  position: absolute;
  height: 270px;
  padding-left: 600px;
  display: flex;
  align-items: center;
}
.quick-contact .qc-content .facebook {
  width: 130px;
  height: 130px;
  border-radius: 50%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.quick-contact .qc-content .facebook img {
  height: 60%;
}
.quick-contact .qc-content .tel {
  margin-left: 300px;
  padding-left: 40px;
  border-left: 5px solid white;
  color: white;
}
.quick-contact .qc-content .tel p {
  font-size: 38px;
  font-weight: 600;
}
.quick-contact .qc-content .tel a p {
  font-size: 50px;
  font-weight: 600;
  color: white;
}
.quick-contact .qc-content .tel a p span {
  font-weight: 200;
}

footer {
  position: absolute;
  left: 0;
  width: 100%;
  height: 450px;
  margin-top: 50px;
  font-weight: 400;
  border-top: 4px solid black;
}
footer .foot-content {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 2560px;
  height: 100%;
  z-index: 2;
  padding: 0 200px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
footer .foot-content .left-box {
  width: 1200px;
  height: 230px;
  display: flex;
  justify-content: space-between;
}
footer .foot-content .left-box .footer-logo img {
  width: 500px;
}
footer .foot-content .left-box .wrapper {
  display: flex;
}
footer .foot-content .left-box .wrapper .company-box {
  padding: 0 30px;
  border-left: 5px solid black;
  font-size: 26px;
  font-weight: 400;
}
footer .foot-content .left-box .wrapper .address-box {
  font-size: 28px;
  font-weight: 400;
}
footer .foot-content .left-box .wrapper .company-box p,
footer .foot-content .left-box .wrapper .address-box p {
  line-height: 1.5em;
}
footer .foot-content .right-box {
  width: 540px;
}
footer .foot-content .right-box ul {
  height: 230px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
footer .foot-content .right-box ul li {
  display: inline-block;
  margin: 8px 0;
  padding-left: 30px;
  border-left: 4px solid black;
  font-size: 28px;
  font-weight: 600;
}
footer .foot-content .right-box ul li a {
  color: black;
}

a.button.black {
  border: 2px white solid;
  transition: all ease-in-out 0.1s;
}

a.button.black:active {
  scale: 0.9;
}

a.button.black:hover {
  background-color: black;
  border: 2px white solid;
  color: white;
  cursor: pointer;
}

a.button.black.white-bg:hover {
  background-color: white;
  border: 2px black solid;
  color: black;
  cursor: pointer;
}

a.button.white {
  border: 2px black solid;
  transition: all ease-in-out 0.1s;
}

a.button.white:active {
  scale: 0.9;
}

a.button.white:hover {
  background-color: white;
  border: 2px black solid;
  color: black;
  cursor: pointer;
}

a.button.white:hover img {
  filter: invert(0%) sepia(0%) saturate(100%) hue-rotate(0deg) brightness(0%) contrast(100%);
}

