/* LIGHTBOX */
#lightbox {
  position: fixed;
  z-index: 1000;
  width: 100%;
  height: 105%;
  background-color: rgba(0, 0, 0, 0.85);
  display: none;
  top: 49%;
  transform: translateY(-50%);
}
#lightbox.active {
  display: flex;
  justify-content: center;
  align-items: center;
}
#lightbox .img-wrapper {
  position: relative;
}
#lightbox .img-wrapper .close-btn {
  position: absolute;
  top: -10px;
  right: -100px;
  padding: 10px;
}
#lightbox .img-wrapper .close-btn img {
  width: 80px;
}
#lightbox .img-wrapper .prev-btn {
  position: absolute;
  top: 50%;
  left: -200px;
  padding: 30px;
  transform: translateY(-50%);
}
#lightbox .img-wrapper .prev-btn img {
  width: 120px;
}
#lightbox .img-wrapper .next-btn {
  position: absolute;
  top: 50%;
  right: -200px;
  padding: 30px;
  transform: translateY(-50%);
}
#lightbox .img-wrapper .next-btn img {
  width: 120px;
}
#lightbox .img-wrapper .img-title p {
  color: white;
  font-size: 36px;
  text-align: center;
}
#lightbox .img-wrapper > img {
  padding: 100px 0 10px 0;
  max-width: 50vw;
  max-height: 85vh;
}

.prev-btn img:hover,
.next-btn img:hover,
.close-btn:hover {
  cursor: pointer;
}

.prev-btn.disabled,
.next-btn.disabled {
  opacity: 0.1;
}

